const ReactDOM = {
    render
}

/**
 * createElement(tag, attrs, child1,child2,......)
 */

/** ÈÄöËøábabelËΩ¨‰πâÁöÑele
 * var ele = React.createElement(
  "div",
  {
    className: "active",
    title: "123"
  },
  "hello, ",
  React.createElement("span", null, "react")
);
 */

function render(vnode, container) {
    console.log(vnode)
    if(vnode === undefined) return;
    //Â¶ÇÊûúvnodeÊòØÂ≠óÁ¨¶‰∏≤
    if(typeof vnode === 'string'){
        //ÂàõÂª∫ÊñáÊú¨ËäÇÁÇπ
        const textNode = document.createTextNode(vnode);
        return container.appendChild(textNode);
    }

    //Âê¶ÂàôÂ∞±ÊòØ‰∏™ËôöÊãüDOMÂØπË±°
    console.log("----v node is ---", vnode.tag, vnode.attrs)
    const {tag, attrs} = vnode; //Ëß£ÊûÑvnode
    //ÂàõÂª∫ËäÇÁÇπÂØπË±°
    const dom = document.createElement(tag);
    if(attrs){
        //ÊúâÂ±ûÊÄß
        Object.keys(attrs).forEach(key=>{
            const value = attrs[key];
            setAttribute(dom, key, value)
        })
    }

    //ÈÄíÂΩí--Ê∏≤ÊüìÂ≠êËäÇÁÇπ
    vnode.childrens.forEach(child=>render(child, dom))
    return container.appendChild(dom);

}

function setAttribute(dom, key, value) {
    //Â∞ÜclassNameËΩ¨‰∏∫class
    if(key === 'className'){
        key = 'class';
    }

    //Â¶ÇÊûúÊòØ‰∫ã‰ª∂ÔºåonClickÔºåonBlurÁ≠â
    if(/on\w+/.test(key)){
        //ËΩ¨Â∞èÂÜô
        key.toLowerCase();
        dom[key] = value || '';
    }else if(key === 'style'){
        if(!value || typeof value === 'string'){
            dom.style.cssText = value || '';
        }else if(value && typeof value === 'object'){
            //for example: {width:20}
            for(let k in value){
                if(typeof value[k] === 'number'){
                    dom.style[k] = value[k] + 'px';
                }else{
                    dom.style[k] = value[k]
                }
            }
        }
    }else if(key in dom){
        dom[key] = value || '';
    }if(value){
        dom.setAttribute(key, value);

    }else{
        dom.removeAttribute(key);
    }
}

export default ReactDOM